.wp-block-create-block-satoh-dojo-slider {

	.carousel-wrapper {
		overflow: hidden;
		width: 100%; /* Adjust based on container width */
	}

	.carousel-container {
		display: flex;
		transition: transform 0.3s ease-in-out;
		width: 300%; /* Adjust based on the number of items */
	}

	.carousel-item {
		flex: 1 0 100%; /* Each item takes full width */
	}

}
